import { useContext } from 'react'
import { ThemeColors } from '@src/utility/context/ThemeColors'
import { ReactComponent as IconBusiness } from '@assets/icons/IconBusiness.svg'
import { ReactComponent as IconThunder } from '@assets/icons/IconThunder.svg'
import { ReactComponent as IconCup } from '@assets/icons/IconCup.svg'
import { ReactComponent as IconDollar } from '@assets/icons/IconDollar.svg'
import { Row, Col } from 'reactstrap'
import CardSummary from '@src/views/ui-elements/cards/summary'
import '@styles/react/libs/charts/apex-charts.scss'

const AnalyticsDashboard = () => {
  const { colors } = useContext(ThemeColors)

    const resData = {
      approve:{
        icon:<IconBusiness/>,
        statsTitle:'Bất Động Sản được duyệt',
        startDate:'15/10',
        formatData:(data)=>124,        
      },
      warning:{
        icon:<IconThunder/>,
        bgColor:'#F16856',
        statsTitle:'Bất động sản cảnh báo',
        startDate:'15/10',
        formatData:(data)=>200,        
      },
      target:{
        icon:<IconCup/>,
        bgColor:'#36CBCB',
        statsTitle:'Hoàn Thành Chỉ Tiêu',
        startDate:'15/10',
        formatData:(data)=>'70%',        
      },
      moneyApprove:{
        icon:<IconDollar/>,
        bgColor:'#55D5F1',
        statsTitle:'Số tiền được duyệt',
        startDate:'15/10',
        formatData:(data)=>'120 tr',        
      },
    }

  return (
    <div id='dashboard-analytics'>
      <Row className='match-height'>
        <Col lg='3' sm='6'>
          <CardSummary {...resData.approve}/>
        </Col>
        <Col lg='3' sm='6'>
          <CardSummary {...resData.warning}/>
        </Col>
        <Col lg='3' sm='6'>
          <CardSummary {...resData.target}/>
        </Col>
        <Col lg='3' sm='6'>
          <CardSummary {...resData.moneyApprove}/>
        </Col>
      </Row>
    </div >
  )
}

export default AnalyticsDashboard
